*,
*::after,
*::before {
  box-sizing: border-box;
}
p {
  margin: 0;
}
ul,
ol {
  list-style: none;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
.text-link {
  color: #005fae;
}
.text-link:hover,
.text-link:active {
  text-decoration-line: underline;
}
.page {
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
}
.page__main {
  margin-left: auto;
  margin-right: auto;
}
.header__first-block {
  min-height: 44px;
  background-color: #005fae;
}
.header__second-block {
  min-height: 44px;
  background: #f7f7f7;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1);
}
.main {
  display: flex;
  justify-content: space-between;
  max-width: 1366px;
  padding-top: 16px;
  padding-left: 33px;
  padding-right: 33px;
}
.main__content {
  margin-right: 20px;
}
@media screen and (max-width: 1160px) {
  .main {
    padding-left: 2.42%;
    padding-right: 2.42%;
  }
  .main__content {
    margin: 0;
  }
}
.content {
  display: flex;
  flex-direction: column;
  max-width: 1120px;
  width: 100%;
}
.content__wrapper {
  display: flex;
}
.content__offer {
  margin-top: 17px;
  margin-right: 20px;
}
.content__description {
  margin-top: 32px;
}
@media screen and (max-width: 1023px) {
  .content__wrapper {
    flex-direction: column;
  }
}
.sidebar {
  display: flex;
  flex-direction: column;
}
.sidebar__banner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 160px;
  height: 320px;
  margin-bottom: 15px;
  background: #fafafa;
}
@media screen and (max-width: 1160px) {
  .sidebar {
    display: none;
  }
}
.company-header {
  display: flex;
  flex-direction: column;
}
.company-header__go-back {
  display: flex;
  align-items: center;
  margin-top: 32px;
}
.company-header__go-back-arrow {
  margin-right: 8px;
}
.company-header__go-back-text {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #424242;
  color: #005fae;
}
.company-header__title {
  position: relative;
  display: flex;
  align-items: baseline;
}
.company-header__title-label {
  margin-top: 6px;
  margin-right: 10px;
}
.company-header__tooltip {
  display: none;
}
.company-header__title-label:hover + .company-header__tooltip {
  display: inline;
  position: absolute;
  top: 55px;
  padding: 5px;
  background-color: #ffffff;
  border: solid 1px #dddddd;
  font-size: 12px;
}
.company-header__title-text {
  margin-top: 16px;
  margin-bottom: 9px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 32px;
  letter-spacing: normal;
  color: #212121;
}
.company-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: space-between;
}
.company-header__views {
  min-width: 70px;
  padding-left: 25px;
  margin-right: 30px;
  margin-top: auto;
  margin-bottom: auto;
  background-image: url("../img/view.svg");
  background-repeat: no-repeat;
  background-position: center left;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #424242;
  color: #999999;
}
.company-header__feedback {
  min-width: 70px;
  padding-left: 25px;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  background-image: url("../img/comment.svg");
  background-repeat: no-repeat;
  background-position: center left;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #424242;
  color: #999999;
}
@media screen and (max-width: 550px) {
  .company-header__button {
    margin-top: 15px;
  }
}
.breadcrumbs {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
}
.breadcrumbs__item {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 16px;
  letter-spacing: normal;
  color: #666666;
}
.breadcrumbs__item:not(:last-child) {
  color: #999999;
}
.breadcrumbs__item:not(:last-child)::after {
  content: "/";
  margin-left: 5px;
  margin-right: 5px;
}
.breadcrumbs__item:not(:last-child):hover {
  text-decoration: underline;
}
.search {
  display: flex;
  max-width: 940px;
  width: 100%;
  height: 40px;
  background: #ffffff;
  border: 2px solid #005fae;
  border-radius: 3px;
}
.search__input {
  flex-grow: 1;
  padding-left: 15px;
}
.search__button {
  width: 64px;
  background: #005fae;
  background-image: url("../img/search.svg");
  background-size: 35%;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
}
.search__button:hover,
.search__button:active {
  opacity: 0.7;
}
.button {
  width: 238px;
  min-height: 40px;
  background-color: #ffffff;
  border: 1px solid #00c346;
  border-radius: 3px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #424242;
  color: #00c346;
  cursor: pointer;
}
.button:hover,
.button:active {
  color: #ffffff;
  background-color: #00c346;
}
.offer {
  position: relative;
}
.offer__title {
  position: relative;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 19px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 28px;
  letter-spacing: normal;
  color: #212121;
}
.offer__title::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #e0e0e0;
}
.offer__table {
  margin-top: 24px;
}
.offer__photo {
  margin-top: 49px;
}
.description {
  display: flex;
  flex-direction: column;
  max-width: 360px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #424242;
}
.description__header {
  display: flex;
  flex-direction: column;
}
.description__wrapper {
  padding-left: 16px;
  padding-right: 16px;
  background: #f0f0f0;
}
.description__wrapper:last-child {
  padding-bottom: 16px;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}
.description__wrapper:nth-child(2) {
  padding-top: 16px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}
.description__logo {
  width: 180px;
  height: auto;
  margin-bottom: 16px;
}
.description__text {
  margin-bottom: 8px;
}
.description__wrapper ul {
  margin: 0;
  margin-bottom: 8px;
}
.description__item::before {
  content: "–";
}
@media screen and (max-width: 1279px) {
  .description {
    max-width: 270px;
  }
}
@media screen and (max-width: 1023px) {
  .description {
    max-width: 100%;
  }
  .description__header {
    flex-direction: row;
    background: #f0f0f0;
  }
  .description__logo {
    width: 120px;
  }
}
@media screen and (max-width: 360px) {
  .description__header {
    flex-direction: column;
    background-color: #ffffff;
  }
}
.table {
  width: 740px;
  border-collapse: collapse;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 16px;
  letter-spacing: normal;
  color: #666666;
}
.table td {
  height: 56px;
}
.table__row-header {
  height: 32px;
}
.table__col-1 {
  text-align: left;
  padding-left: 93px;
  background: #f0f0f0;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
.table__col-2 {
  width: 55px;
  background: #f0f0f0;
  text-align: right;
}
.table__col-3 {
  width: 60px;
  padding-top: 7px;
  background: #f0f0f0;
  text-align: right;
}
.table__col-4 {
  width: 238px;
  padding-left: 10px;
  background: #f0f0f0;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  text-align: left;
}
.table__row {
  border-bottom: 1px solid #e0e0e0;
}
.table__ad {
  display: flex;
  align-items: center;
  min-height: 54px;
}
.table__label {
  min-width: 60px;
  min-height: 24px;
  margin-left: 16px;
  margin-right: 16px;
  text-align: center;
  border-radius: 3px;
  color: #ffffff;
  line-height: 24px;
}
.table__label--sale {
  background-color: #6199c6;
}
.table__label--buy {
  background-color: #ee6c4d;
}
.table__text {
  color: #005fae;
}
.table__date {
  text-align: right;
}
.table__view {
  text-align: right;
}
.table__date-label {
  display: none;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #424242;
}
.table__author {
  display: flex;
  align-items: center;
  padding-left: 10px;
  text-align: left;
}
.table__img {
  margin-right: 10px;
}
@media screen and (max-width: 1279px) {
  .table {
    width: 100%;
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media screen and (max-width: 767px) {
  .table__header {
    display: none;
  }
  .table__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  .table td {
    height: auto;
  }
  .table__ad {
    min-height: 35px;
  }
  .table__cell-ad {
    width: 100%;
  }
  .table__label {
    margin-left: 0;
  }
  .table__date {
    width: 100px;
    margin-right: 20px;
    text-align: left;
  }
  .table__date-label {
    display: inline;
    margin-right: 10px;
  }
  .table__view {
    width: 70px;
    margin-right: auto;
    padding-left: 20px;
    background-image: url("../img/view.svg");
    background-repeat: no-repeat;
    background-position: center left;
  }
  .table__author {
    width: 230px;
    margin-bottom: 5px;
    padding-left: 0;
  }
}
.photo__wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
  margin-left: -4px;
}
.photo__img {
  height: 120px;
  margin: 4px;
  cursor: pointer;
}
.photo__img:hover {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  transform: scale(1.01);
}
@media screen and (max-width: 767px) {
  .photo__wrapper {
    justify-content: center;
  }
  .photo__img {
    display: none;
  }
  .photo__img--active {
    display: block;
  }
}
